/**
 * 
 * @param {String|Number} num 
 * @param {String} 
 */
export function formatPrice(num, length = 2) {
  if (!num) { // 
    return 0
  }
  const numArr = num.split('.')
  if (numArr.length > 1 && length) { // 
    numArr[1] = numArr[1].slice(0, length)
    num = numArr[0] + '.' + numArr[1]
  }
  if (!numArr[0].length) { // 
    num = ''
  }
  return num
}
