<!--  -->
<template>
  <el-input
    ref="input"
    :value="inputValue"
    v-bind="$attrs"
    v-on="inputListeners"
  >
    <template #prefix>
      <slot name="prefix" />
    </template>
    <template #suffix>
      <slot name="suffix" />
    </template>
    <template #prepend>
      <slot name="prepend" />
    </template>
    <template #append>
      <slot name="append" />
    </template>
    <!-- <slot slot="prefix" name="prefix" />
    <slot slot="suffix" name="suffix" />
    <slot slot="prepend" name="prepend" />
    <slot slot="append" name="append" /> -->
  </el-input>
</template>

<script>
import { formatPrice } from './utils.js'

export default {
  name: 'NumberInput',
  props: {
    value: {
      required: true,
      type: [String, Number],
      default: ''
    },
    // 
    positive: {
      required: false,
      type: Boolean,
      defalut: true
    },
    /**
     * 
     */
    remain: {
      required: false,
      type: [Number, String],
      default: 0
    },
    /**
     * 
     */
    max: {
      required: false,
      type: [Number, String],
      default: ''
    },
    /**
     * 
     */
    min: {
      required: false,
      type: [Number, String],
      default: ''
    }
  },
  data() {
    return {
      inputValue: ''
    }
  },
  computed: {
    inputListeners() {
      return Object.assign({}, this.$listeners, {
        input: this.handleInputValue
      })
    }
  },
  watch: {
    /**
     * value
     */
    value: {
      immediate: true,
      handler: function(newValue) {
        this.inputValue = newValue
      }
    }
  },
  methods: {
    /**
     * 
     */
    focus() {
      this.$refs['input'].focus()
    },

    handleInputValue(value) {
      // 
      if (this.positive) {
        value = value.replace(/[^0-9.]/g, '')
      } else {
        value = value.replace(/[^0-9.-]/g, '')
      }

      // 
      if (this.remain) {
        value = value && formatPrice(value, this.remain) // 
      } else {
        value = value.replace(/[^\d]/g, '')
      }

      // 
      const verifyRresult = this.verifyValue(value)
      this.$emit('change', verifyRresult.value)
      this.$emit('input', verifyRresult.value)
    },

    /**
     * 
     */
    verifyValue(value) {
      let max = this.max
      let min = this.min
      let flag = true
      let resultValue = value

      // 
      if (max !== '') {
        max = +max
        flag = value <= max

        if (!flag) {
          resultValue = max
        }
      }

      // flag  true
      if (flag && min !== '') {
        min = +min
        flag = value >= min

        if (!flag) {
          resultValue = min
        }
      }

      return {
        status: flag,
        value: resultValue
      }
    }
  }
}
</script>
